import React, {FC} from 'react';

import {homeAPI} from "../../../../api/HomeAppService";
import {addUserResponseType} from "../../../../common/interfaces/Interfaces";

interface IItemUser {
    user: addUserResponseType
    isLoadingUsers: boolean
}

export const ItemUser: FC<IItemUser> = ({user, isLoadingUsers}) => {

    const [deleteUser] = homeAPI.useDeleteUserMutation()

    const deleteUserHandler = (bindId: number) => {
        deleteUser(bindId)
    }

    return (
        <div style={{
            width: "250px",
            height: "250px",
            border: "2px solid purple",
            display: 'flex',
            flexDirection: 'column',
            justifyContent: 'space-between',
            alignItems: "center",
            margin: '20px',
        }}>
            <div>
                
            </div>
            <div>
                <div> : {user.name}</div>
                <div> : {user.phone}</div>
                <div>Email : {user.email}</div>
                <div>ID: {user.id}</div>
            </div>
            <div>
                <button onClick={() => deleteUserHandler(user.bindId)} disabled={isLoadingUsers}> </button>
            </div>
        </div>
    );
};
